@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  src: local("Muller light"), url("../fonts/mullerlight-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "PlayfairDisplay";
  font-style: normal;
  font-weight: 700;
  src: local("PlayfairDisplay bold"), url("../fonts/playfair_display_bold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: local("Raleway light"), url("../fonts/raleway-light-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: local("Raleway semibold"), url("../fonts/raleway-semibold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: local("Raleway extrabold"), url("../fonts/raleway-extrabold-webfont.woff2") format("woff2");
}

body,
html {
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  font-family: "Raleway", "Muller", "PlayfairDisplay", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 1px;
  color: #5e5e5e;
  background-color: #bbb;
  /* #fdfaf3 */
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.wrapper {
  max-width: 1170px;
  margin: 0 37px;
}

.centering {
  display: flex;
  justify-content: center;
}

.slider-button,
.button-arrow,
.play,
.menu,
.arrow {
  font-size: 0;
  cursor: pointer;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #7d7d7d;
  font-size: 1.125em;
  line-height: 30px;
}

.submit-orange {
  cursor: pointer;
}

.phone-number {
  font-family: "Muller";
  color: #252525;
  font-size: 0.875em;
  letter-spacing: 1.5px;
}

.intro-text {
  color: #ffffff;
  font-size: 0.875em;
  line-height: 20px;
}

h1 {
  font-family: "PlayfairDisplay";
  font-size: 3.938em;
  line-height: 58px;
  color: #ffffff;
  letter-spacing: 1.5px;
}

.button-hover,
.submit-orange {
  display: block;
  font-weight: 700;
  font-size: 0.938em;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
}

.button-hover {
  border: 2px solid #fff;
}

.button-hover:hover {
  background-color: #f8a25a;
  text-decoration: none;
  color: #fff;
}

.button-hover:active {
  background-color: #ef862f;
}

.headline {
  font-family: "PlayfairDisplay";
  color: #00203a;
  font-size: 3em;
}

.navigation-ref {
  text-transform: uppercase;
  font-size: 1.25em;
  line-height: 42px;
}

.features-list .feature-item .feature-headline {
  font-weight: 500;
  font-size: 1.375em;
  color: #1a292f;
}

.projects-photo {
  cursor: pointer;
}

.services-ref.current {
  font-weight: 600;
}

.current-ref,
.services-ref.current,
.service-column span {
  color: #00203a;
}

.service-headline {
  font-family: "PlayfairDisplay";
  font-weight: 400;
  font-size: 2.1em;
  color: #00203a;
}

.service-column span,
.service-text {
  font-size: 1.125em;
  line-height: 30px;
  letter-spacing: 0;
}

.video-text {
  font-family: "Raleway";
  font-size: 1.125em;
  color: #ffffff;
}

.steps-headline {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 17px;
  color: #252525;
}

.steps-number {
  font-size: 0.75em;
}

.form-headline {
  font-family: "PlayfairDisplay";
  font-size: 2.813em;
  color: #ffffff;
}

.form-text,
.user-text,
.form-text2 {
  font-size: 0.875em;
  line-height: 20px;
  color: #ffffff;
}

.client-name {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 30px;
  color: #252525;
}

.feedbacks-name {
  font-weight: 700;
  color: #efb172;
}

.sgn1,
.sgn2 {
  color: #ffc487;
}

.main-header {
  padding-top: 83px;
  margin-bottom: 216px;
}

.main-header .wrapper {
  position: relative;
}

.main-header .wrapper .slider-box {
  position: relative;
}

.main-header .wrapper .slider-box .slider-wrap .main-slider {
  width: 100%;
}

.main-header .wrapper .slider-box .main-bg {
  position: absolute;
  top: -83px;
  left: -100px;
}

.main-header .wrapper .slider-box .slider-buttons {
  position: absolute;
  right: 6px;
  bottom: -47px;
}

.main-header .wrapper .slider-box .slider-buttons .slider-button {
  border: none;
  height: 3px;
  width: 26px;
  margin-left: 5px;
  background-color: #cdd3cf;
}

.main-header .wrapper .slider-box .slider-buttons .slider-button.current {
  background-color: #1a444a;
}

.main-header .wrapper .slider-box .slider-buttons .slider-button:hover {
  background-color: #1a444a;
}

.main-header .wrapper .intro-column {
  position: absolute;
  max-width: 325px;
  top: 170px;
  left: 94px;
}

.main-header .wrapper .intro-column .intro-text {
  margin-top: 26px;
  word-wrap: break-word;
}

.main-header .wrapper .intro-column .button-hover {
  margin-top: 35px;
  padding: 20px 78px 17px 28px;
  max-width: 278px;
  position: relative;
}

.main-header .wrapper .intro-column .button-hover::after {
  content: "";
  position: absolute;
  background-image: url(../img/Arrow-OutRight-icon.png);
  background-repeat: no-repeat;
  background-position-x: 18px;
  background-position-y: 2px;
  height: 22px;
  width: 100px;
}

.main-header .wrapper .menu-wrap {
  position: absolute;
  left: 2px;
  top: -54px;
}

.main-header .wrapper .menu-wrap .show-nav {
  display: block;
  background-image: url("../img/bg.png");
  position: absolute;
  top: 54px;
  left: 0;
  width: 1161px;
  height: 595px;
}

.main-header .wrapper .navigation-wrap {
  display: none;
}

.main-header .wrapper .navigation-wrap .navigation-list {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding-top: 110px;
}

.main-header .wrapper .navigation-wrap .navigation-list .navigation-item {
  margin-top: 12px;
}

.main-header .wrapper .navigation-wrap .navigation-list .navigation-item .navigation-ref:hover {
  color: #f8a25a;
}

.main-header .wrapper .navigation-wrap .navigation-list .navigation-item .navigation-ref:active {
  color: #ffffff;
}

.main-header .logo {
  position: absolute;
  top: -61px;
  left: 51%;
  transform: translateX(-50%);
}

.main-header .phone-wrap {
  position: absolute;
  top: -58px;
  right: 4px;
}

.main-header .phone-wrap:hover span {
  font-weight: 700;
}

.main-header .phone-wrap:hover svg {
  animation: phone-animation 1s infinite ease-in;
}

@keyframes phone-animation {
  0% {
    transform: scale(2);
  }

  50% {
    transform: rotate(360deg);
  }

  100% {
    transform: scale(2);
  }
}

.main-header .social-list {
  display: flex;
  position: absolute;
  left: 0;
  bottom: -56px;
}

.main-header .social-list .social-item {
  margin-right: 36px;
}

.main-header .social-list .social-item:hover svg {
  transform: scale(1.6);
}

.main-header .social-list .social-item:active svg {
  opacity: 0.3;
}

.main-header .social-list .social-item .social-ref {
  display: block;
}

.features {
  margin-bottom: 138px;
}

.features-list {
  display: flex;
  flex-wrap: wrap;
}

.feature-item {
  width: 25%;
  padding-right: 15px;
}

.feature-item:nth-child(2),
.feature-item:nth-child(4) {
  margin-top: 210px;
}

.feature-item-wrap .number {
  display: block;
  color: #fde3c9;
  font-family: "PlayfairDisplay";
  font-size: 85px;
  margin-bottom: 40px;
}

.feature-item-wrap .feature-headline {
  display: block;
  max-width: 280px;
  margin-bottom: 12px;
  letter-spacing: 0;
}

.portfolio {
  margin-bottom: 136px;
}

.portfolio-nav {
  display: flex;
  flex-wrap: wrap;
  padding-top: 64px;
  padding-bottom: 54px;
}

.portfolio-item {
  margin-right: 56px;
}

.portfolio-item .ref {
  letter-spacing: 0;
}

.portfolio-item .ref:hover {
  color: #00203a;
  font-weight: 600;
}

.portfolio-item .ref:active {
  border-bottom: 1px solid #000;
  padding-bottom: 10px;
}

.portfolio-item .current-ref {
  font-weight: 600;
  border-bottom: 1px solid #000;
  padding-bottom: 10px;
  letter-spacing: 0;
}

.projects-slider {
  display: flex;
}

.projects-photo {
  position: relative;
  width: 25%;
  height: 552px;
}

.projects-photo:hover {
  width: 40%;
}

.projects-photo:hover .overlay,
.projects-photo:hover .design,
.projects-photo:hover .rotate {
  display: none;
}

.projects-photo:hover .arrow {
  filter: invert(100%);
  transform: scale(1);
}

.projects-photo:hover a {
  display: block;
  position: absolute;
  top: 54%;
  left: 52%;
  transform: translate(-50%, -50%);
}

.projects-photo:active svg {
  opacity: 0.5;
}

.projects-photo:active img {
  opacity: 0.8;
}

.projects-photo img {
  width: 100%;
}

.projects-photo .overlay {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0.8;
  z-index: 5;
}

.projects-photo a {
  display: none;
}

.design {
  position: absolute;
  top: 0;
  left: 20;
  color: #ffffff;
  font-weight: 500;
  line-height: 22px;
  padding-left: 28px;
  padding-top: 26px;
  max-width: 180px;
  z-index: 10;
}

.rotate {
  position: absolute;
  bottom: 31px;
  left: 18px;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  font-size: 0.875em;
  color: #ffffff;
  z-index: 10;
}

.arrow {
  background-color: transparent;
  border: none;
  position: absolute;
  width: 38px;
  height: 12px;
  top: 247px;
  z-index: 10;
}

.arrow.left {
  background-image: url(../img/arrow2.png);
  left: 32px;
}

.arrow.right {
  background-image: url(../img/arrow1.png);
  right: 32px;
}

.services-wrap {
  display: flex;
  padding-top: 79px;
}

.services-nav {
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  padding-right: 54px;
  justify-content: space-between;
  min-width: 276px;
  margin-top: 10px;
}

.services-item {
  line-height: 21px;
  max-width: 187px;
  flex-shrink: 0;
}

.services-item.three {
  max-width: 187px;
}

.services-item:hover a {
  color: #00203a;
  font-weight: 600;
}

.services-item .services-ref {
  line-height: 21px;
  display: inline-block;
  letter-spacing: 0;
}

.services-item .services-ref.current {
  min-width: 300px;
  padding-left: 24px;
  line-height: 19px;
  letter-spacing: 0;
}

.service-column {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  max-height: 712px;
}

.service-column .button-white {
  max-width: 216px;
  padding: 17px 38px;
  color: #00203a;
  border: 2px solid #00203a;
  max-height: 58px;
  margin-top: 56px;
  align-self: end;
}

.service-column .button-white:hover {
  color: #ffffff;
  border-color: #ffffff;
}

.service-column .services-photo {
  margin-bottom: 60px;
}

.service-column .service-col1,
.service-column .service-col2 {
  display: flex;
  justify-content: space-between;
}

.service-column .service-col1 {
  margin-bottom: 14px;
}

.service-column .service-text {
  max-width: 498px;
}

.feedbacks .wrapper {
  position: relative;
}